import Toast from './Toast'

Toast.install = (Vue) => {
  const ToastConstructor = Vue.extend(Toast)
  const $vmToast = new ToastConstructor().$mount()

  document.body.appendChild($vmToast.$el)

  Vue.toast = Vue.prototype.$toast = (content, options) => {
    if ($vmToast.show) return

    Object.assign($vmToast.$data, $vmToast.$options.data(), options)

    $vmToast.show = true
    $vmToast.content = content

    if ($vmToast.time > 0) {
      setTimeout(() => {
        Vue.hideToast()
      }, $vmToast.time)
    }
  }

  Vue.hideToast = Vue.prototype.$hideToast = () => {
    $vmToast.show = false
  }
}

export default Toast
