import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/components/Layout'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  // base: process.env.BASE_URL,

  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout,
      children: [
        {
          path: '/',
          name: 'home',
          component: () => import('./views/Home'),
          meta: {
            title: ''
          }
        },
        {
          path: '/tools',
          name: 'tools',
          redirect: '/',
          component: {
            render: h => (
              <keep-alive>
                <router-view />
              </keep-alive>
            )
          },
          children: [
            {
              path: '/tools/hupai',
              name: 'hupai',
              component: () => import('./views/tools/Hupai'),
              meta: {
                title: ''
              }
            },
            {
              path: '/tools/hupai/how',
              name: 'hupaiHow',
              component: () => import('./views/tools/HupaiHow'),
              meta: {
                title: '',
                navHidden: true
              }
            },
            {
              path: '/tools/clean',
              name: 'clean',
              component: () => import('./views/tools/Clean'),
              meta: {
                title: ''
              }
            }
          ]
        },
        {
          path: '/love-stories',
          name: 'loveStory',
          redirect: '/love-stories/calendar',
          component: {
            render: h => (
              <keep-alive>
                <router-view />
              </keep-alive>
            )
          },
          children: [
            {
              path: '/love-stories/calendar',
              name: 'loveCalendar',
              component: () => import('./views/lovestory/Calendar'),
              meta: {
                title: '',
                navHidden: true
              }
            }
          ]
        }
      ]
    }, {
      path: '*',
      name: '404',
      component: () => import('./views/NotFound'),
      meta: {
        title: 'Page not found'
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  document.title = to.meta.title

  next()
})

export default router
