import moment from 'moment'

const mixin = {
  data () {
    return {
      bgColor: '#323840'
    }
  },
  filters: {
    /**
     * 
     * @param {*} timestamp 
     * @param {*} format 
     */
    formatTime (timestamp, format) {
      if (!timestamp) return ''

      return moment(timestamp).format(format || 'YYYY/MM/DD')
    }
  },
  methods: {
    // 
    pageTo (path) {
      this.$router.push(path)
    }
  }
}

export default mixin
